body {
  color: #fff;
  background: linear-gradient(135deg, #1f003a, #0a0218);
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.glass {
  backdrop-filter: blur(16px);
  background: #fff3;
  border-radius: 16px;
  transition: transform .2s ease-in-out, box-shadow .2s;
  box-shadow: 0 8px 24px #0006;
}

button {
  color: #fff;
  background: linear-gradient(to right, #6a21ab, #9f33ee);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  transition: transform .3s, box-shadow .3s;
}

button:hover {
  background: linear-gradient(to right, #9333ee, #6a21ab);
  transform: scale(1.05);
  box-shadow: 0 6px 12px #0006;
}
